.hero {
    position: relative;
    top: 55px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 50vh;
    min-height: 270px;
    padding: 40px 25px 40px;
    background-color: $ppal-color;
    font-family: $ppal-font;
    &__h1{
    color: white;
    font-family: $ppal-font;
    font-weight: 700;
    font-size: 30px;
    line-height: 34px;
    margin-bottom: 10px;
    }
    &__h2{
      color: white;
      font-family: $ppal-font;
      font-weight: 700;
      font-size: 16px;
      line-height: 22px;
      margin-bottom: 20px;
    }
    &__plans{
      display: flex;
    justify-content: center;
    align-items: center;
        &--link {
        color: white;
        background-color: rgb(30, 172, 73);
        text-decoration: none;
        font-family: $ppal-font;
        font-weight: 600;
        font-size: 13px;
        line-height: 13px;
        display: flex;
        height: 40px;
        width: 250px;
        border-radius: 30px;
        justify-content: center;
        align-items: center;
        margin-bottom: 20px;
        }
    }
    &__terms{
    display: inline-block;
    color: white;
    font-family: $ppal-font;
    font-size: 9px;
    line-height: 18px;
    }
  }
  

